import { Context, ContextTypes } from "./context";

var types;

export function format(s, context) {
  types = ContextTypes;
  let c = context;
  return s.replace(/{(\w+)}/g, () => c.table);
}

export function base64(uriComponent) {
  return window.btoa(unescape(encodeURIComponent(uriComponent)));
}

export const uri = "data:application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;base64,";
// "data:application/vnd.ms-excel;base64,";
export const template =
  '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-mic' +
  'rosoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><meta cha' +
  'rset="UTF-8"><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:Exce' +
  "lWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/>" +
  "</x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></" +
  "xml><![endif]--></head><body>{table}</body></html>";

